:root {
    --specBG: conic-gradient(from 90deg at 50% 50%, #9b0b0b, #eb3232);
    --somColor: #97081c;
    --lightSomColor: #e30c2a;
    --thightDBgwhite: white;
    --theaderColors: #9b0000;
    --tdataColors: rgb(204, 204, 204);
    --tfontSize: 1rem;
    --tdataSize: 2rem;
    --thightDC: rgb(0, 82, 114);
    --thightDBg: rgb(161, 161, 161);
}

@import url('https://fonts.googleapis.com/css2?family=Indie+Flower&family=Share+Tech&family=Ubuntu:wght@300&display=swap');

* {
    margin: 0;
    padding: 0;

}

body {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Share Tech', sans-serif;
    /* font-family: "Ubuntu", sans-serif; */
    font-size: 1rem;
    background: black;
    color: var(--somColor);

}

::-webkit-scrollbar {
    width: 0.8rem;
    height: 0.8rem;
}

::-webkit-scrollbar-button {
    height: 0;
    width: 0;
}

::-webkit-scrollbar-track {
    background: rgb(255, 255, 255);
    border: 0.3rem solid rgb(255, 255, 255);
}

::-webkit-scrollbar-thumb {
    border-radius: 0.9rem;
    background: grey;
    background-clip: padding-box;
    border: 0.3rem solid rgb(255, 255, 255);
    /* box-shadow: 0 -100vh 0 100vh var(--lightSomColor); */
}

body::-webkit-scrollbar {
    width: 0.8rem;
    height: 0.8rem;
}

body::-webkit-scrollbar-track:vertical {
    border: none;
    border-right: 0.3rem solid rgb(255, 255, 255);
}

body::-webkit-scrollbar-thumb:vertical {
    background: var(--lightSomColor);
    border: none;
    border-right: 0.3rem solid rgb(255, 255, 255);
}



