button {
    background: none;
    border: none;
    outline: none;
    color: white;
    padding: 0.5rem;
    cursor: pointer;
    background: black;
    border-radius: 0.2rem;
    transition: 0.3s all ease-out;
}

button:hover {
    opacity: 0.5;
}

input {
    background: white;
    padding: 0.3rem;
    border-radius: 0.5rem;
    transition: 0.3s all ease-out;
    color: black;
    outline: none;
    border: none;
    margin: 0.5rem;
    font-size: 1rem;
}

input:focus {
    outline: none;
}

.fullbg {
    display: flex;
    justify-content: center;
    align-items: center;
    /* flex-direction: column; */
    /* background: wheat; */
    padding-top: 4rem;
    padding-bottom: 4rem;
    width: 100%;
    min-height: 100vh;
    box-sizing: border-box;
    height: 100vh;
    gap: 5vw;
    /* backdrop-filter: blur(0.2rem); */
}

.fullbgHOME {
    /* flex-direction: column; */
    margin-top: 0;
}

.algos {
    display: flex;
    flex-direction: column;
    background: transparent;
    padding: 1rem;
    margin-top: 1.5rem;
    border-radius: 0.5rem;
    /* backdrop-filter: blur(0.1rem); */
}

.algos button {
    background: var(--specBG);
    color: white;
    margin: 0.5rem;
    font-size: 1.5rem;
    padding: 1rem;
    transition: 0.3s all ease-out;
    border-radius: 0.5rem;
    transition: 0.3s all ease-out;
}

.algos button:hover {
    /* background: #999999; */
    opacity: 0.7;
}

.left-side {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 67.5vw;
    height: 70%;
    /* resize: both; */
    /* margin-right: 5vw; */
}

.left-side .simulation {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    overflow: auto;
    background: white;
    box-shadow: 0 0 0.4rem black;
    /* resize: both; */
    width: 100%;
    height: 100%;
}

.right-side {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    height: 70%;
    width: 22.5vw;
}



/* .fullbgHOME {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 1rem;
    color: white;
} */

.Table {
    display: table-row-group;
    font-size: var(--tfontSize);
}

.Table .row {
    display: table-row;
    color: black;
}

.Table .BTitle {
    background: var(--theaderColors);
    color: white;
}

.allSteps {
    position: relative;
    height: auto;
    width: 100%;
    padding: 0.5rem;
    overflow-y: scroll;
    overflow-x: hidden;
    scroll-behavior: smooth;
    font-size: 1rem;
}

.right-side p {
    font-size: 1.2rem;
    margin-bottom: 0.4rem;
}

.right-side .enHead {
    font-size: 1.5rem;
    margin-bottom: 0.5rem;
}

.right-side .pgreen {
    color: white;
    background: green;
    padding: 0.3rem;
    text-align: center;
    font-size: 2rem;
    /* margin: 0 0.5rem; */
}

.right-side .inputsDiv {
    margin-bottom: 1rem;
}

.stepCard {
    position: relative;
    background: black;
    background: var(--somColor);
    color: rgb(0, 24, 129);
    /* color: var(--somColor); */
    border: 0.2rem solid rgb(218, 218, 218);
    /* display: flex; */
    padding: 1rem;
    padding-bottom: 2rem;
    margin: 0.5rem 0;
    border-radius: 0.3rem;
    text-align: left;
    box-shadow: inset 0 0 0.2rem grey;
}



.stepCard .stepH {
    font-weight: 650;
    margin-right: 0.5rem;
}

.stepCard input {
    background: white;
    padding: 0.3rem;
    border-radius: 0.5rem;
    transition: 0.3s all ease-out;
    color: black;
    outline: none;
    border: none;
    margin: 0.5rem;
    font-size: 1rem;
    min-width: 2rem;
    max-width: 4rem;
    box-shadow: 0 0 0.1rem 0.1rem darkblue;
}

.stepCard input:focus {
    box-shadow: 0 0 0.1rem 0.2rem darkblue;
}

.algoDone input {
    box-shadow: 0 0 0.1rem 0.1rem darkred;
}

.algoDone input:focus {
    box-shadow: 0 0 0.1rem 0.2rem darkred;
}


.stepCard .stepDoneIcon {
    position: absolute;
    right: 0.5rem;
    bottom: 0.5rem;
    font-size: 1.2rem;
    transition: 0.1s all ease-out;
    opacity: 0.8;
    border: 0.2rem solid rgb(197, 197, 197);
    background: rgb(255, 255, 255);
    border-radius: 50%;
}

.stepCard .inStepDivs1 {
    background: white;
    padding: 0.3rem;
    box-shadow: inset 0 0 0.2rem black;
    margin-bottom: 0.5rem;
}



.goanime {
    animation: goanimate 1s normal infinite;
}


.Table .row .hight,
.lcsTable .row .hight {
    /* background: var(--thightDBg); */
    /* color: var(--thightDC); */
    animation: anHight 0.4s 2;
}

.Table .row .Fhight,
.lcsTable .row .Fhight {
    /* background: var(--thightDBg); */
    /* color: var(--thightDC); */
    animation: anFHight 0.4s 2;
}

.Table .row .hightStr,
.lcsTable .row .hightStr {
    /* background: var(--thightDBg); */
    /* color: var(--thightDC); */
}

@keyframes goanimate {
    0% {
        /* opacity: 0.5; */
        /* background: rgb(49, 49, 49); */
        background: rgb(189, 189, 189);
        border: 0.2rem solid rgb(255, 255, 255);
    }

    100% {
        background: black;
        background: white;
        border: 0.2rem solid rgb(0, 38, 255);
    }
}

@keyframes anHight {
    0% {
        background: var(--tdataColors);
        color: white;
    }

    100% {
        background: var(--thightDBgwhite);
        color: var(--thightDC);
    }
}

@keyframes anFHight {
    0% {
        background: var(--tdataColors);
        color: white;
    }

    100% {
        background: var(--thightDBgwhite);
        color: red;
    }
}


.algoDone {
    background: rgb(0, 0, 0);
    background: whitesmoke;
    color: var(--somColor);
    /* border: 0.2rem solid rgb(110, 110, 255); */
    border: 0.2rem solid var(--somColor);
}


.dNone {
    display: none;
}

.spec {
    margin: 0.5rem 0;
    /* background: rgb(73, 73, 73); */
    background: conic-gradient(from 90deg at 50% 50%, rgba(0, 16, 197, 1), rgba(108, 103, 250, 1));
    font-size: 1.2rem;
    padding: 0.3rem;
    color: white;
    opacity: 1;
    margin: 0.3rem;
    box-shadow: 0 0 0rem 0.1rem rgb(110, 124, 255);
    transition: 0.3s all ease-out;
}

.spec:focus{
    transform: scale(0.5);
    box-shadow: 0 0 0rem 0.1rem rgb(110, 124, 255);
}

.spec:disabled {
    background: var(--specBG);
    /* opacity: 0.5; */
    cursor: not-allowed;
    box-shadow: 0 0 0rem 0.1rem rgb(255, 110, 110);
}

.spec:disabled:hover {
    box-shadow: 0 0 0rem 0.1rem rgb(255, 110, 110);
}

.spec:hover {
    opacity: 1;
    color: white;
    /* background: conic-gradient(from 270deg at 50% 50%, #ff0037, #ff4870); */
    box-shadow: 0 0 0rem 0.25rem rgb(110, 124, 255);
}

.stepCard .content .cbutton {
    background: none;
    padding: 0.2rem;
    font-size: 1.1rem;
    font-weight: 700;
    color: var(--somColor);
    color: black;
    /* text-shadow: 0 0 0.2rem var(--lightSomColor); */
    -webkit-text-stroke-color: rgb(255, 108, 108);
    -webkit-text-stroke-width: 0.01rem;
    background: rgb(255, 247, 233);
    transition: 0.3s all ease-out;
    /* border: 0.2rem solid rgb(0, 0, 172); */

}

.stepCard .content .cbutton:hover {
    /* color: var(--lightSomColor); */
    opacity: 0.8;
}

.restartb {
    width: 100%;
    background: var(--specBG);
    box-shadow: 0 0 0rem 0.1rem rgb(255, 110, 110);
}

.restartb:hover{
    opacity: 1;
    box-shadow: 0 0 0rem 0.25rem rgb(255, 110, 110);
}

.writeCheck {
    color: rgb(0, 24, 129);
}

.Table .row .bStrs,
.lcsTable .row .bStrs {
    background: green;
    color: white;
}

@media (max-width: 700px) {
    * {
        font-size: 1rem;
    }

    .fullbg {
        flex-direction: column-reverse;
        gap: 5rem;
        height: 100%;
    }

    .left-side {
        width: 80vw;
        height: 25rem;
    }

    .right-side {
        width: 80vw;
    }

    .fullbgHOME,
    .fullbgHOME h1 {
        flex-direction: column;
        font-size: 1.5rem;
    }
}

@media (max-width: 400px) {
    * {
        font-size: 0.8rem;
    }

    .fullbgHOME,
    .fullbgHOME h1 {
        flex-direction: column;
        font-size: 1.5rem;
    }
}

@media (max-width: 300px) {
    * {
        font-size: 0.7rem;
    }

    .fullbgHOME,
    .fullbgHOME h1 {
        flex-direction: column;
        font-size: 1.5rem;
    }
}